	module.exports = {"data":{"markdownRemark":{"frontmatter":{"block":"Basic Node and Express"},"html":"<h2>Introduction to the Basic Node and Express Challenges</h2>\n<p>Node.js is a JavaScript tool that allows developers to write backend (server-side) programs in JavaScript. Node.js comes with a handful of built-in modules—small, independent programs—that help facilitate this purpose. Some of the core modules include:<br><br><ul><li>HTTP: a module that acts as a server</li><li>File System: a module that reads and modifies files</li><li>Path: a module for working with directory and file paths</li><li>Assertion Testing: a module that checks code against prescribed constraints</li></ul><br>Express, while not included with Node.js, is another module often used with it. Express runs between the server created by Node.js and the frontend pages of a web application. Express also handles an application's routing. Routing directs users to the correct page based on their interaction with the application.<br><br>While there are alternatives to using Express, its simplicity makes it a good place to begin when learning the interaction between a backend powered by Node.js and the frontend.\nWorking on these challenges will involve you writing your code on Glitch on our starter project. After completing each challenge you can copy your public Glitch url (to the homepage of your app) into the challenge screen to test it! Optionally you may choose to write your project on another platform but it must be publicly visible for our testing.<br>Start this project on Glitch using <a href='https://glitch.com/#!/import/github/freeCodeCamp/boilerplate-express/'>this link</a> or clone <a href='https://github.com/freeCodeCamp/boilerplate-express/'>this repository</a> on GitHub! If you use Glitch, remember to save the link to your project somewhere safe!</p>"},"allChallengeNode":{"edges":[{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/meet-the-node-console"},"title":"Meet the Node console"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/start-a-working-express-server"},"title":"Start a Working Express Server"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/serve-an-html-file"},"title":"Serve an HTML File"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/serve-static-assets"},"title":"Serve Static Assets"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/serve-json-on-a-specific-route"},"title":"Serve JSON on a Specific Route"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/use-the--env-file"},"title":"Use the .env File"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/implement-a-root-level-request-logger-middleware"},"title":"Implement a Root-Level Request Logger Middleware"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/chain-middleware-to-create-a-time-server"},"title":"Chain Middleware to Create a Time Server"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/get-route-parameter-input-from-the-client"},"title":"Get Route Parameter Input from the Client"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/get-query-parameter-input-from-the-client"},"title":"Get Query Parameter Input from the Client"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/use-body-parser-to-parse-post-requests"},"title":"Use body-parser to Parse POST Requests"}},{"node":{"fields":{"slug":"/apis-and-microservices/basic-node-and-express/get-data-from-post-requests"},"title":"Get Data from POST Requests"}}]}},"pathContext":{"block":"basic-node-and-express","slug":"/apis-and-microservices/basic-node-and-express"}}

